@media (width > 48em) {
  .col-2 {
    flex: 0 1 calc(50% - 1.5rem);
  }
}

@media (width > 48em) {
  .col-3 {
    flex: 0 1 calc(33.3333333333% - 1.5rem);
  }
}

@media (width > 48em) {
  .col-4 {
    flex: 0 1 calc(50% - 6rem);
  }
}
@media (width > 70em) {
  .col-4 {
    flex: 0 1 calc(25% - 6rem);
  }
}

.technology {
  padding-block: 5rem;
}
.technology .d-flex-layout {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
@media (width > 48em) {
  .technology .d-flex-layout {
    display: flex;
  }
}